function Article(name, file) {
  return (
    <li href="" className="articleButton" filename={file}>
      <p>{name}</p>
      <div className="slidebutton">
        <span>
          <i class="fas fa-arrow-right"></i>
        </span>
      </div>
    </li>
  );
}

let articles = [];

let myarticles = [{ name: "why I use vim", file: "whyiusevim.md" }];

for (let i = 0; i < myarticles.length; i++) {
  articles.push(Article(myarticles[i].name, myarticles[i].file));
}

function ArticleList() {
  return (
    <div className="articleList">
      <ul>
        {
          //Article("Why do i use vim")
          articles
        }
      </ul>
    </div>
  );
}

export default ArticleList;
