import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';

function Data(props) {

  const file_name = props.file;
    const [post, setPost] = useState('');

    useEffect(() => {
        import(`./${file_name}`)
            .then(res => {
                fetch(res.default)
                    .then(res => res.text())
                    .then(res => setPost(res))
                    .catch(err => console.log(err));
            })
            .catch(err => console.log(err));
    });

  return(
      <ReactMarkdown>
          {post}
      </ReactMarkdown>
  );
}

export default Data;
